import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import {Router} from '@angular/router'





@Component({
  selector: 'app-gl1',
  templateUrl: './gl1.component.html',
  styleUrls: ['./gl1.component.css']
})
export class Gl1Component implements OnInit {

  constructor(public location: Location, public router:Router) { }
  public url:any;
  ngOnInit() {
    this.log
    this.submit
  }
 
public onSwipeRight(event){
this.url=this.router.navigate(['getloan'])
return this.url
}
  
public onSwipeLeft($event){
this.url=this.router.navigate(['/gl2'])
return this.url
}

public log(x){
  console.log(x)
}

public submit(f){
console.log(f)
}
//to go back to page
public goBack(){
  this.location.back()
}

}
