import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router"
import { Location } from '@angular/common';

@Component({
  selector: 'app-gl4',
  templateUrl: './gl4.component.html',
  styleUrls: ['./gl4.component.css']
})
export class Gl4Component implements OnInit {
  public url:any;

  constructor(private route:Router,private location:Location) { }

  ngOnInit() {
  }
  public onSwipeRight($event){
    this.url = this.route.navigate(['/gl3'])
    return this.url
    }
    public onSwipeLeft($event){
    this.url = this.route.navigate(['/gl5'])
    return this.url
    }

    public goBack(){
      this.location.back();
      //window.history.back()
  
  }
}
