import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import {ActivatedRoute, Router} from "@angular/router";
import {WebcamImage, WebcamInitError, WebcamUtil} from 'ngx-webcam';
import {Subject} from 'rxjs/Subject';
import {Observable} from 'rxjs/Observable';

//import { NgCircleProgressModule } from 'ng-circle-progress';
import * as $ from 'jquery'

//import { Stream } from 'stream';

@Component({
  selector: 'app-gl3',
  templateUrl: './gl3.component.html',
  styleUrls: ['./gl3.component.css']
})
export class Gl3Component implements OnInit {

  constructor(public location: Location,public route:Router) { }
  public showWebcam = true;
  public allowCameraSwitch = true;
  public multipleWebcamsAvailable = false;
  public deviceId: string;
  public videoOptions: MediaTrackConstraints = {
    width: {ideal: 1024},
    height: {ideal: 576}
  };
  public errors: WebcamInitError[] = [];

  // latest snapshot
  public webcamImage: WebcamImage = null;
  public facingMode: string = 'environment';

  // webcam snapshot trigger
  private trigger: Subject<void> = new Subject<void>();
  // switch to next / previous / specific webcam; true/false: forward/backwards, string: deviceId
  private nextWebcam: Subject<boolean|string> = new Subject<boolean|string>();

  public url:any;

  public  a ={
    video: true,
    audio:false
  }
  
  ngOnInit() {
   
   
  const b = document.getElementsByClassName('btn-cam');
 
  $('.btn-cam').addClass('hidden');
  
//swape button
$('.cam').on('click',function(){
  $('.step-3-btn').addClass('hidden')
    $('.btn-cam').removeClass('hidden');
})

$(".btn-cam-cancel-btn").on('click',function(e){
  $('.step-3-btn').removeClass('hidden')
    $('.btn-cam').addClass('hidden');
})
$()

}
public startcamera(){
  let video = <HTMLVideoElement>(document.getElementById("#videoElement"));
  if(navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
    // Not adding `{ audio: true }` since we only want video now
    navigator.mediaDevices.getUserMedia(this.a).then(stream=> {
        //video.src = window.URL.createObjectURL(stream);
        
        video.srcObject =stream
        //video.srcObject= stream;
        video.play()
    });
}
 }

 public onSwipeRight($event){
this.url = this.route.navigate(['/gl2'])
return this.url
}
public onSwipeLeft($event){
this.url = this.route.navigate(['/gl4'])
return this.url
}


  public triggerSnapshot(): void {
    this.trigger.next();
  }

  public toggleWebcam(): void {
    this.showWebcam = !this.showWebcam;
  }

  public handleInitError(error: WebcamInitError): void {
    this.errors.push(error);
  }

  public showNextWebcam(directionOrDeviceId: boolean|string): void {
    // true => move forward through devices
    // false => move backwards through devices
    // string => move to device with given deviceId
    this.nextWebcam.next(directionOrDeviceId);
  }

  public handleImage(webcamImage: WebcamImage): void {
    console.info('received webcam image', webcamImage);
    this.webcamImage = webcamImage;
  }

  public cameraWasSwitched(deviceId: string): void {
    console.log('active device: ' + deviceId);
    this.deviceId = deviceId;
  }

  public get triggerObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

  public get nextWebcamObservable(): Observable<boolean|string> {
    return this.nextWebcam.asObservable();
  }

 

  

  public goBack(){
    this.location.back();
    //window.history.back()

}
}





