import { Component, OnInit } from '@angular/core';
import{Router} from '@angular/router'

@Component({
  selector: 'app-get-loan',
  templateUrl: './get-loan.component.html',
  styleUrls: ['./get-loan.component.css']
})
export class GetLoanComponent implements OnInit {

  constructor(private router:Router) { }

  public url:any;
 

  ngOnInit() {
}

public onSwipeLeft($event){
  this.url=this.router.navigate(['/gl1'])
  return this.url
  }
  public onSwipeRight($event){
    this.url = this.router.navigate(['/main'])
    return this.url
    }
 
}
